import React, { Component } from 'react';
import { connect } from "react-redux"
import fire from '../config/firebaseConfig'
import Header from '../components/Header.js'
import Router from 'next/router'
import checkAuthAndRefresh from '../dbActions/checkAuth'
import Loading from './loading'
import Logo from '../components/Logo.js'
import {Form, Button, Col, Navbar, Card} from 'react-bootstrap'
import Link from 'next/link'

class Login extends Component {
    state = {
        email: '',
        password: ''
    }

    handleChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
        console.log(this.state.email);
        console.log(this.state.password);
    }

    componentWillMount() {
        if (!this.props.authChecked) checkAuthAndRefresh(this.props.dispatch)
    }
	
    handleSubmit = (e) => {
        const db = fire.firestore();
        fire.auth().signInWithEmailAndPassword(this.state.email, this.state.password
        ).then(
            (user) => {
                db.collection("users").doc(user.user.uid).get().then((doc) => {
                    Router.push('/');
                })
            }).catch(
                (err) => {
                    event.preventDefault();
                    event.stopPropagation();
                    this.props.dispatch({ type: 'LOGIN_ERR', msg: err.message });
                });
    }

    render() {
        if (!this.props.authChecked) {
            return (<Loading />)
        }
        return (
            <div style={{width: '100%'}}>
                <Header isLogin={true} />
                <div style={{height: '100vh', width: '100%', backgroundImage: 'linear-gradient(red, yellow)'}} className="wrapper d-flex flex-column align-items-center justify-content-center">
                <Logo use='login' />
                    <main style={{maxWidth: '400px'}} className="m-2">
                            
                            <Card>
                                <Card.Body>
                                    <Card.Text>
                                    <Form>
                                        <Form.Group controlId="formBasicEmail">
                                            <Form.Label>כתובת דואר אלקטרוני</Form.Label>
                                            <Form.Control type="email" id="email" onChange={this.handleChange} />
                                        </Form.Group>
                                        <Form.Group controlId="formBasicPassword">
                                            <Form.Label>סיסמה</Form.Label>
                                            <Form.Control type="password" id="password" onChange={this.handleChange} />
                                        </Form.Group>
                                        <p><Link href="#">שכחתי סיסמה</Link></p>
                                        <Button onClick={this.handleSubmit} variant="primary">
                                            התחברות
                                        </Button>
                                        {this.props.loginErr ?
                                        <div className='mt-4' style={{ textAlign: 'left', direction: 'ltr', color: 'red' }}>
                                             {this.props.loginErr}
                                        </div> : ''}
                                        </Form>
                                    </Card.Text>
                                </Card.Body>
                                </Card>
                    </main>
                </div></div>);
        }
}

export default connect(state => state)(Login);